// style
import './style/sass/index.scss';

// script
import ReactDOM from 'react-dom';
import Application from './App';
import { closePlaceholder, documentReady, removePlaceholder } from './utls/jsCommon';
import { changeThemeOnTime } from './utls/theme';

const initAH = async function () {
  await documentReady();
  changeThemeOnTime();
};

const onLoadedReact = async () => {
  await closePlaceholder();
  removePlaceholder();
};

const renderReactDOM = (onLoaded) => {
  ReactDOM.render(<Application onLoaded={onLoaded} />, document.getElementById('ah-root'));
};

initAH().then(() => renderReactDOM(onLoadedReact));
