export function mobileOnly() {
  return window.outerWidth < 568;
}

let transTimeout = null;
export const transitionForDocument = () => {
  if (transTimeout) clearTimeout(transTimeout);
  const { documentElement } = document;
  documentElement.classList.add('transition');
  transTimeout = setTimeout(() => documentElement.classList.remove('transition'), 1000);
};

export const closePlaceholder = () => {
  return new Promise((resolve) => {
    const theLoadingEle = document.querySelector('.loading');
    const theCloseCls = 'loading--close';
    theLoadingEle.classList.add(theCloseCls);
    setTimeout(resolve, 2000);
  });
};

export const removePlaceholder = () => {
  const theLoadingEle = document.querySelector('.loading');
  theLoadingEle.remove();
};

export const documentReady = () => {
  return new Promise((resolve) => {
    document.addEventListener('readystatechange', () => {
      switch (document.readyState) {
        // ------------------------------------------------------------
        // The document is still loading.
        case 'loading':
          break;
        // ------------------------------------------------------------
        // ------------------------------------------------------------
        // The document has finished loading. We can now access the DOM elements.
        // The document has been parsed but sub-resources such as scripts, images, stylesheets and frames are still loading.
        case 'interactive':
          break;
        // ------------------------------------------------------------
        // ------------------------------------------------------------
        // The page is fully loaded.
        // The document and all sub-resources have finished loading. The state indicates that the load event is about to fire.
        case 'complete':
          resolve();
          break;
        // ------------------------------------------------------------
        default: {
          console.log('def');
        }
      }
    });
  });
};
